/// <reference path="../node_modules/chrome-extension-async/chrome-extension-async.d.ts" />
import 'chrome-extension-async'

require("regenerator-runtime/runtime");

import Message from './interfaces/Message';
import { MDCSlider } from '@material/slider';

const sliderElem: HTMLDivElement = document.querySelector('#volume-slider');
const radioElems: HTMLDivElement = document.querySelectorAll('.mdc-radio a');
const sliderMaxElem: HTMLDivElement = document.querySelector('#slider-max span');

const disableButtonElem: HTMLDivElement = document.querySelector('#disable_control');

const slider = new MDCSlider(sliderElem);

void (async () => {
  // Hide the slider until we know the initial volume
  sliderElem.style.opacity = '0'

  const initialValue = await getActiveTabVolume()
  slider.value = initialValue * 100;

  adjustSliderMax();

  if(initialValue != 1){
    setActiveTabVolume(initialValue);
  }

  sliderElem.style.opacity = '1';

  for (var i = 0; i < radioElems.length; i++) {
         if(((radioElems[i].id == "mute" && initialValue==0) || radioElems[i].innerText == initialValue+"x") && !radioElems[i].classList.contains('checked')) radioElems[i].classList.add('checked');
         else radioElems[i].classList.remove('checked');
     }

})()

disableButtonElem.addEventListener('click',  async function(event) {
                         //disableButtonElem.classList.add('checked');
                         /*const tabId =  await getActiveTabId();
                         await ((chrome.runtime.sendMessage({ name: 'dispose-tab', tabId}))
                         */
                         chrome.runtime.reload();

                      });

//handle shortcuts
for (var i = 0; i < radioElems.length; i++) {
    radioElems[i].addEventListener('click', function(event) {
        var value = event.target.innerText;
        if(value == "volume_off") value = "0";
        else value = value.slice(0, -1);
        setActiveTabVolume(value);

        slider.value = value * 100;

        adjustSliderMax();

        for (var i = 0; i < radioElems.length; i++) {
               radioElems[i].classList.remove('checked');
           }
        if(event.target.innerText == "volume_off") event.target.parentElement.classList.add('checked');
        else event.target.classList.add('checked');
    });
}


//handle slider
slider.listen('MDCSlider:input', () => {
    const value = slider.value / 100

   for (var i = 0; i < radioElems.length; i++) {
       if(((radioElems[i].id == "mute" && value==0) || radioElems[i].innerText == value+"x") && !radioElems[i].classList.contains('checked')) radioElems[i].classList.add('checked');
       else radioElems[i].classList.remove('checked');
   }

  adjustSliderMax();
  setActiveTabVolume(value);
})

function adjustSliderMax(){
    if(slider.value>=slider.max-100){
        slider.max = slider.max+100
        sliderMaxElem.innerText = slider.max/200
      }
      else if(slider.value<=0.4*slider.max && slider.max >=600){
        slider.max = slider.max-100
        sliderMaxElem.innerText = slider.max/200
      }
}

async function getActiveTabVolume () {
  const tabId = await getActiveTabId()
  const message: Message = { name: 'get-tab-volume', tabId }
  return chrome.runtime.sendMessage(message)
}

async function setActiveTabVolume (value: number) {
  const tabId = await getActiveTabId()
  const message: Message = { name: 'set-tab-volume', tabId, value }
  const res = await chrome.runtime.sendMessage(message)
    /*
    if(value == 1){
          return chrome.runtime.sendMessage({ name: 'dispose-tab', tabId });
     }
     */
     return res;
}

async function getActiveTabId () {
  const [activeTab] = await chrome.tabs.query({ active: true, currentWindow: true })
  return activeTab.id
}
